'use client';

import { useEffect, useState } from 'react';
import { Brain, Zap, Eye, BookOpen } from 'lucide-react';
import type { LearningProfile, TutorRecommendation } from '@/types/training';

interface Props {
  onStartChat?: () => void;
}

export function TutorProfile({ onStartChat }: Props) {
  const [profile, setProfile] = useState<LearningProfile | null>(null);
  const [recommendations, setRecommendations] = useState<TutorRecommendation[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const res = await fetch('/api/partners/tutor/profile');
        const data = await res.json();
        setProfile(data.profile);
        setRecommendations(data.recommendations);
      } catch (error) {
        console.error('Failed to fetch profile:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchProfile();
  }, []);

  if (loading) {
    return <div className="text-neutral">Cargando perfil...</div>;
  }

  if (!profile) {
    return <div className="text-neutral">No profile found</div>;
  }

  return (
    <div className="space-y-6">
      {/* Profile Header */}
      <div className="bg-dark-surface border border-white/10 rounded-2xl p-6">
        <h2 className="text-2xl font-bold mb-4">Tu Perfil de Aprendizaje</h2>

        <div className="grid md:grid-cols-4 gap-4">
          {/* Nivel */}
          <div className="bg-white/5 rounded-lg p-4">
            <div className="flex items-center gap-2 mb-2">
              <Brain className="w-5 h-5 text-blue-400" />
              <span className="text-sm text-neutral">Nivel</span>
            </div>
            <p className="text-lg font-semibold capitalize">{profile.skillLevel}</p>
          </div>

          {/* Pace */}
          <div className="bg-white/5 rounded-lg p-4">
            <div className="flex items-center gap-2 mb-2">
              <Zap className="w-5 h-5 text-yellow-400" />
              <span className="text-sm text-neutral">Ritmo</span>
            </div>
            <p className="text-lg font-semibold capitalize">{profile.preferredPace}</p>
          </div>

          {/* Style */}
          <div className="bg-white/5 rounded-lg p-4">
            <div className="flex items-center gap-2 mb-2">
              <Eye className="w-5 h-5 text-purple-400" />
              <span className="text-sm text-neutral">Estilo</span>
            </div>
            <p className="text-lg font-semibold capitalize">{profile.learningStyle}</p>
          </div>

          {/* Conversations */}
          <div className="bg-white/5 rounded-lg p-4">
            <div className="flex items-center gap-2 mb-2">
              <BookOpen className="w-5 h-5 text-green-400" />
              <span className="text-sm text-neutral">Conversaciones</span>
            </div>
            <p className="text-lg font-semibold">{profile.conversationCount}</p>
          </div>
        </div>
      </div>

      {/* Topics */}
      <div className="grid md:grid-cols-2 gap-6">
        {/* Struggling Topics */}
        <div className="bg-dark-surface border border-red-500/30 rounded-2xl p-6">
          <h3 className="font-semibold mb-3 text-red-400">Necesitas Ayuda Con</h3>
          {profile.strugglingTopics.length > 0 ? (
            <div className="space-y-2">
              {profile.strugglingTopics.map(topic => (
                <div
                  key={topic}
                  className="bg-red-500/10 border border-red-500/20 rounded px-3 py-2 text-sm"
                >
                  {topic}
                </div>
              ))}
            </div>
          ) : (
            <p className="text-neutral">¡Vas bien en todos los temas!</p>
          )}
        </div>

        {/* Strong Topics */}
        <div className="bg-dark-surface border border-green-500/30 rounded-2xl p-6">
          <h3 className="font-semibold mb-3 text-green-400">Dominas</h3>
          {profile.strongTopics.length > 0 ? (
            <div className="space-y-2">
              {profile.strongTopics.map(topic => (
                <div
                  key={topic}
                  className="bg-green-500/10 border border-green-500/20 rounded px-3 py-2 text-sm"
                >
                  {topic}
                </div>
              ))}
            </div>
          ) : (
            <p className="text-neutral">Empieza a aprender para desbloquear temas</p>
          )}
        </div>
      </div>

      {/* Recommendations */}
      {recommendations.length > 0 && (
        <div className="bg-dark-surface border border-white/10 rounded-2xl p-6">
          <h3 className="font-semibold mb-4">Recomendaciones Personalizadas</h3>
          <div className="space-y-3">
            {recommendations.slice(0, 3).map(rec => (
              <div
                key={rec.type}
                className="bg-white/5 rounded-lg p-4 flex justify-between items-start"
              >
                <div>
                  <p className="font-medium mb-1">{rec.topic}</p>
                  <p className="text-sm text-neutral">{rec.reason}</p>
                </div>
                <span
                  className={`px-2 py-1 text-xs rounded capitalize ${
                    rec.priority === 'high'
                      ? 'bg-red-500/20 text-red-200'
                      : rec.priority === 'medium'
                        ? 'bg-yellow-500/20 text-yellow-200'
                        : 'bg-green-500/20 text-green-200'
                  }`}
                >
                  {rec.priority}
                </span>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* CTA */}
      <button
        onClick={onStartChat}
        className="w-full bg-primary hover:bg-primary-dark text-white font-medium py-3 rounded-lg transition"
      >
        Chatear con el Tutor
      </button>
    </div>
  );
}
