'use client';

import { useEffect, useState } from 'react';
import { BookOpen, Lightbulb, Award, ArrowRight } from 'lucide-react';
import { SovraLoader } from '@/components/ui';
import type { LearningProfile } from '@/types/training';

export function TrainingDashboard() {
  const [profile, setProfile] = useState<LearningProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    coursesCompleted: 0,
    tutorialsCompleted: 0,
    tutorConversations: 0,
  });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch('/api/partners/tutor/profile');
        if (res.ok) {
          const data = await res.json();
          setProfile(data.profile);
          setStats({
            coursesCompleted: data.profile?.completedCourses?.length || 0,
            tutorialsCompleted: 0, // Will update when we have tutorial progress API
            tutorConversations: data.profile?.conversationCount || 0,
          });
        }
      } catch (error) {
        console.error('Failed to fetch profile:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="flex h-40 items-center justify-center">
        <SovraLoader size="md" className="text-[var(--color-primary)]" />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Welcome Banner */}
      <div className="bg-gradient-to-r from-primary/10 to-primary/5 border border-primary/20 rounded-2xl p-6">
        <h2 className="text-2xl font-bold mb-2">¡Bienvenido al Centro de Entrenamiento!</h2>
        <p className="text-neutral">
          Elige tu ruta de aprendizaje: cursos estructurados, tutor personalizado o tutoriales interactivos
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid md:grid-cols-3 gap-4">
        <div className="bg-dark-surface border border-white/10 rounded-2xl p-4">
          <div className="flex items-center justify-between">
            <div>
              <span className="text-neutral text-sm">Cursos Completados</span>
              <p className="text-3xl font-bold mt-1">{stats.coursesCompleted}</p>
            </div>
            <BookOpen className="w-8 h-8 text-primary opacity-50" />
          </div>
        </div>

        <div className="bg-dark-surface border border-white/10 rounded-2xl p-4">
          <div className="flex items-center justify-between">
            <div>
              <span className="text-neutral text-sm">Conversaciones Tutor</span>
              <p className="text-3xl font-bold mt-1">{stats.tutorConversations}</p>
            </div>
            <Lightbulb className="w-8 h-8 text-primary opacity-50" />
          </div>
        </div>

        <div className="bg-dark-surface border border-white/10 rounded-2xl p-4">
          <div className="flex items-center justify-between">
            <div>
              <span className="text-neutral text-sm">Certificaciones</span>
              <p className="text-3xl font-bold mt-1">{stats.coursesCompleted}</p>
            </div>
            <Award className="w-8 h-8 text-primary opacity-50" />
          </div>
        </div>
      </div>

      {/* Learning Profile Quick View */}
      {profile && (
        <div className="bg-dark-surface border border-white/10 rounded-2xl p-6">
          <h3 className="font-semibold mb-4">Tu Perfil de Aprendizaje</h3>
          <div className="grid md:grid-cols-4 gap-4">
            <div className="bg-white/5 rounded p-3">
              <span className="text-xs text-neutral">Nivel</span>
              <p className="font-medium capitalize">{profile.skillLevel}</p>
            </div>
            <div className="bg-white/5 rounded p-3">
              <span className="text-xs text-neutral">Ritmo</span>
              <p className="font-medium capitalize">{profile.preferredPace}</p>
            </div>
            <div className="bg-white/5 rounded p-3">
              <span className="text-xs text-neutral">Estilo</span>
              <p className="font-medium capitalize">{profile.learningStyle}</p>
            </div>
            <div className="bg-white/5 rounded p-3">
              <span className="text-xs text-neutral">Conversaciones</span>
              <p className="font-medium">{profile.conversationCount}</p>
            </div>
          </div>
        </div>
      )}

      {/* Quick Actions */}
      <div className="grid md:grid-cols-3 gap-4">
        <a
          href="#modules"
          className="bg-dark-surface border border-white/10 hover:border-primary/50 rounded-2xl p-6 transition group"
        >
          <div className="flex items-center justify-between mb-2">
            <BookOpen className="w-6 h-6 text-primary" />
            <ArrowRight className="w-4 h-4 text-neutral group-hover:text-primary transition" />
          </div>
          <h4 className="font-semibold mb-1">Cursos</h4>
          <p className="text-sm text-neutral">
            Completa cursos estructurados y gana certificaciones verificables
          </p>
        </a>

        <a
          href="#tutor"
          className="bg-dark-surface border border-white/10 hover:border-primary/50 rounded-2xl p-6 transition group"
        >
          <div className="flex items-center justify-between mb-2">
            <Lightbulb className="w-6 h-6 text-primary" />
            <ArrowRight className="w-4 h-4 text-neutral group-hover:text-primary transition" />
          </div>
          <h4 className="font-semibold mb-1">Tutor IA</h4>
          <p className="text-sm text-neutral">
            Recibe ayuda personalizada con Claude, tu tutor de IA
          </p>
        </a>

        <a
          href="/partners/portal/tutorials-center"
          className="bg-dark-surface border border-white/10 hover:border-primary/50 rounded-2xl p-6 transition group"
        >
          <div className="flex items-center justify-between mb-2">
            <Lightbulb className="w-6 h-6 text-primary" />
            <ArrowRight className="w-4 h-4 text-neutral group-hover:text-primary transition" />
          </div>
          <h4 className="font-semibold mb-1">Tutoriales</h4>
          <p className="text-sm text-neutral">
            Aprende haciendo con tutoriales interactivos paso-a-paso
          </p>
        </a>
      </div>
    </div>
  );
}
